.login-container1{
    width:100%;
    height:70vh;
    align-items: center;
    z-index: 1;
}

.heading{
    font-family: cursive;
    font-size: 38px;
    text-shadow: 1px 2px 5px rgb(57, 27, 228);
    margin-top: 2%;
    margin-left:2%;
   
}
.login-items{
     width:30%;
     height:90%;
     background-color: rgb(107, 173, 235);
     margin-left:20%;
     justify-content: center;
     align-items: center;
     border-radius:5%;
     display:flexbox;
     box-shadow: 0px 0px 10px 0 rgba(96, 37, 233, 0.945);
    
}

.image img{
    width:70%;
    height:70%;
    border-radius: 50%;
}

.login-items input{ 
        outline:none;
        background-color: rgb(255, 251, 251);
        background:transparent;
        color:rgb(37, 42, 51);
        border-style: none;
        border-bottom: 2px solid rgb(104, 6, 6);
        text-align: center;
        padding:2%;
        font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        margin-top: 5%;

}

.login-items button{ 
    width:70px;
    height:40px;
    background-color: black;
    color:white;
    outline:none;
    font-family: cursive;
    justify-content: space-between;
    text-align: center;
    padding:2%;
    border-style: none;
    border:none;
    border-radius: 20%;
    margin-top: 5%;
    margin:5%;
    transition: 500ms ease-in;
}
.login-items button:hover{
    background-color: rgb(80, 72, 72);
    box-shadow: 0px 0px 10px 0 rgba(19, 19, 20, 0.945);
    
}
.forgot{
    
    text-decoration: none;
    font-family: cursive;
    color:rgb(255, 255, 255)
}
