.content-box{
    height: 70vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(201, 245, 248);
    color:rgb(226, 217, 217);
    margin-top: 50px;

}
.card{
    position: relative;
    width: 270px;
    height: 230px;
    border-radius: 10px;
    box-shadow: 0 0 6px 0 rgba(0, 163, 244, 0.938);
    transition: 0.3s;
    padding: 10px 20px;
    background: #d8f1f4;
    cursor: pointer;
}
.card:hover{
    height: 350px;
}
.img-box{
    position: relative;
    width:100%;
    height:100%;
    transform: translateY(-60px);
    z-index:100;
}
img{
    width: 100%;
    
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(234, 142, 4, 0.3);
}
.content1 h3{
  margin-top: -50px;  
text-align: center;
color:black;
     
}
.content1 h4{
     
  text-align: center;
  color:rgb(59, 53, 53);
       
  }
.content1 p{ 
    padding: 10px 10px;
    margin-top: 20px;
    text-align: center;
    transform: translateY(-10px);
    opacity: 0;
    transition: 0.3s;
    color: black;
}
.card:hover .content1 p{
    opacity: 1;
    margin-top: 80%;
    transform: translateY(-230px);
}
.card:hover .content1 h3{
    opacity: 1;
    transform: translateY(-50px);
}
.card:hover .content1 h4{
    opacity: 1;
    transform: translateY(-50px);
}

