
.navigation-links{
    display:flex;
    list-style-type: none;
    padding: 12px;
    margin-top: 1%;
   
}
.navigation-bar{
    width: 100%;
    height:50px;

    background-color:  #f3a668;
    color:rgb(226, 240, 38);
    align-items: center;
    text-align: center;
    display: flex;
    justify-content: space-between;
    
    
}
.navigation-links a{
    text-decoration: none;
    color:white;
    font-size: 20px;
    text-align: center;
    align-items: center;
    padding: 15px;
    transition:100ms ease-in;
}
.navigation-links a:hover,.login a:hover{
    color:rgb(68, 7, 2)
}
.login a{
    color:white;
    font-size: 25px;
    text-decoration: none;
    padding-right:30px;
    transition: 100ms ease-in;
    
}
