.mylogo123{
   
    background: white;
    height: 30vh;
    align-items: center;
    margin: 0;
    padding: 0;
    overflow: hidden;
    
    
}
.slide-track{
    display: flex;
    width: calc(190px*20);
    animation: scroll 90s linear infinite;
   
}
@keyframes scroll {
    0%{
        transform: translate(0);
    }
    100%{
        transform: translateX(calc(-150px*10));
    }
}
.col1{
    height: 150px;
    width: 150px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 40px;
    cursor: pointer;
    box-shadow: none;
    
    
}
.col1 img{
    position: relative;
    width:150%;
    height:100%;
}

