.attend-row{
  display:flex;
  height: 650px;
  background: rgb(248, 218, 218);
}
.attend-col1{
  width: 25%;
  
}
.attend-col2{
  width: 75%;
  background: rgb(241, 201, 178);
}
/*first column*/
.sidebar{
  padding-top: .5rem;
  transition: all 300ms ease;
  text-align: center;
}
.attend-logo2 img{
  position: relative;
 
  margin-top: 30px;
 border-radius: 50%;
 width: 150px;
 height: 150px;
}
.line5{
  position: relative;
 
  margin-top: 20px;
  font-weight: bold;
  font-size: 20px;
  color: #000;
}
.line6{
  position: relative;
  
  font-weight: bold;
  font-size: 20px;
  color: #000;
}

.attend-menu{
margin: 10px;

}
.menu-item{
  height: 50px;
  width: 250px;
 font-size: 22px;
 color: #000;
 margin: 20px;
 margin-left: 50px;
 margin-top: 27px;
  
 
 box-shadow: 0 0 20px 0 rgba(228, 86, 121, 0.938);
 background: rgb(240, 155, 119);
 border-radius: 25px;

}

.icon{
position: relative;
top: 5px;


}
.icon link{
  text-decoration: none;
  color: #000;
}
.menu-item span{
  position: relative;
  top: 2px;

  font-family: 'Lobster', cursive;
  
}
.link a{

  text-decoration: none;
  color: #000;
}


.menu-item:hover{
  cursor: pointer;
  color: rgb(1, 12, 15);
  background: rgb(245, 223, 234);
}
/*second column*/
.navigation-bar3{
  width: 100%;
  height:50px;
  position: relative;
  top: 20px;

  background-color: rgba(223, 237, 243, 0.67);
  color:rgb(223, 106, 16);
  align-items: center;
  text-align: center;
  display: flex;
  justify-content: space-between;
  
  
}
.navigation-links2 span{
  text-decoration: none;
  color:rgb(18, 2, 2);
  position: relative;
 
  font-size: 30px;
  font-family: 'Sofia';
  font-weight: bold;
  text-align: center;
  align-items: center;
  top: 10px;
  padding: 5px;
  transition:100ms ease-in;
}

.maindash2{
  height: 650px;
 width: 1150px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background: rgba(168, 226, 252, 0.67);
}
.cards{
  transform: translateY(25px);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color:rgb(10, 10, 10);
  margin-top: 25px;
}
.cards2{
   transform: translateY(25px);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color:rgb(10, 10, 10);
  margin-top: 25px;
}

.dash-card{
  position: relative;
  bottom: 30px;

  width: 210px;
  height: 250px;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(10, 200, 234, 0.938);
  transition: 0.3s;
  padding: 10px 20px;
  background: #ecf3f8;
  cursor: pointer;
  
}
.dash-card2{
  position: relative;
  bottom: 30px;
    
  width: 212px;
  height: 230px;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(10, 200, 234, 0.938);
  transition: 0.3s;
  padding: 10px 20px;
  background: #ecf3f8;
  cursor: pointer;
  
  
  
}
.attend-logo h3{
  position:relative;
  left: 65px;
  font-family: 'Lobster', cursive;
}
.attend-logo span {
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 5px;
}
.attend-logo img{
  position: relative;
  left: 10px;
}


.dash-card span{
  
  font-size: 20px;
  font-family: 'Lobster', cursive;
}
.dash-card2 span{
  
  position: relative;
  top: 10px;
  font-size: 20px;
  font-family: 'Lobster', cursive;
}

.pages{
    display: flex;
    background: rgba(204, 227, 236, 0.67);
    position: relative;
   
    width: 1150px;
  }
  .Reg-info {
    margin: 0;
    padding: 0;
    flex: 4;
    
  }
  .up-pic{
    margin: 10px;
    padding: 0;
    align-items: flex-end;
    justify-content: center;
    display: flex;
    flex-direction: row;
  
  }
  .face-logo img{
  width: 150px;
  height:150px;
  }
  .up-btn{
  justify-content: center;
  }
  .up-btn button{
  margin: 10px 10px;
  padding: 5px;
  background-color: rgb(5, 145, 145);
  color: white;
  font-size: 15px;
  border-radius: 5px;
  transition:500ms;
  border: none;
  
  }
  .up-btn button:hover{
    color: #000;
    background-color: rgb(146, 219, 219);
  }
  
  
  .reg-detail{
    
    position: relative;
    top: 40px;
      height: 75vh;
      display: flex !important;
      flex-direction: column;
      align-items: center;
      max-width: 50%;
      width: 100%;
      margin:auto;
      
      /*margin-top: 50px;
      background: rgb(212, 132, 132);*/
    }
    .reg-detail h1{
      position: relative;
      top: 15px;
    }
  .reg-detail > form {
    
    height: 75vh;
      width: 100%;
     
      padding: 10px;
     font-size: 20px;
      border-radius: 5px;
      background: rgb(219, 248, 250);
      box-shadow: 0 0 30px 0 rgba(100, 181, 248, 0.938);
      border-radius: 5%;
    }
    .reg-detail input{
      border-radius: 7px;
      font-size: 20px;
      border: none;
      border-bottom: 2px solid rgb(100, 41, 41);
      text-align: center;
    }
    .field{
      margin: 10px;
      font-size: 30px;
    }
    
  .reg-detail p{color: crimson;}
  
  .reg-detail label{
    
      font-size: 23px;
      display: inline-block;
      width: 240px;}
  .reg-detail h1{ 
    margin-bottom: 30px;
    position: relative;
    left: 120px;
  }
  
  .reg-btn button{
    text-align: center;
    position: relative;
   
    font-family: 'Lobster';
    margin: 10px 10px;
    padding: 5px;
    width: 100px;
    font-size: 20px;
    border-radius: 25px;
    box-shadow:  0 0 20px 0 rgb(2, 177, 151);
    transition:500ms;
    border: none;
    text-decoration: none;
    background-color: rgb(73, 213, 218);
  }
  .reg-btn button:hover{
    color: #000;
    background-color: rgb(146, 219, 219);
  }
  .reg-btn  {
    text-align: center;
          margin-top: 40px;
          
  }
  .link a{

    text-decoration: none;
    
}
