.login-container1{
    width:100%;
    height:70vh;
    display:flex;
    align-items: center;
   
    
    
}
.image img{
    width:90%;
    height:90%;
    margin-left: 20%;
    border-radius: 50%;
    box-shadow: 0px 0px 10px 0 rgba(137, 136, 139, 0.945);
    
}
 .heading{
    font-family: cursive;
    font-size: 38px;
    margin-left:66%;
    
    text-shadow: 1px 2px 5px rgb(57, 27, 228);
}
.signup-items{
     width:30%;
     height:100%;
     background-color: rgb(107, 173, 235);
     margin-left:20%;
     justify-content: center;
     align-items: center;
     border-radius:5% ;
     box-shadow: 0px 0px 10px 0 rgba(96, 37, 233, 0.945);
     transition:500ms ease-in;
    
}
.image2 img {
    width:25%;
    height:25%;
    border-radius: 50%;
    margin-top: 5%;
}

.signup-items input{ 
        outline:none;
        background-color: rgb(255, 251, 251);
        background:transparent;
        color:rgb(37, 42, 51);
        border-style: none;
        border-bottom: 2px solid rgb(104, 6, 6);
        text-align: center;
        padding:2%;
        font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        margin-top: 4%;

}

.signup-items button{ 
    width:70px;
    height:40px;
    background-color: black;
    color:white;
    outline:none;
    font-family: cursive;
    justify-content: space-between;
    text-align: center;
    padding:2%;
    border-style: none;
    border:none;
    border-radius: 20%;
    
    margin:3%;
    transition: 500ms ease-in;
}
.signup-items button:hover{
    background-color: rgb(80, 72, 72);
    box-shadow: 0px 0px 10px 0 rgba(19, 19, 20, 0.945);
    
}
.forgot{
    
    text-decoration: none;
    font-family: cursive;
    color:rgb(253, 253, 253)
}