.attend-row{
    display:flex;
    height: 650px;
    background: rgb(248, 218, 218);
}
.attend-col1{
    width: 25%;
    
}
.attend-col2{
    width: 75%;
    background: rgb(241, 201, 178);
}
/*first column*/
.sidebar{
    padding-top: .5rem;
    transition: all 300ms ease;
    text-align: center;
}
.attend-logo2 img{
    position: relative;
   
    margin-top: 30px;
   border-radius: 50%;
   width: 150px;
   height: 150px;
}
.line5{
    position: relative;
   
    margin-top: 20px;
    font-weight: bold;
    font-size: 20px;
    color: #000;
}
.line6{
    position: relative;
    
    font-weight: bold;
    font-size: 20px;
    color: #000;
}

.attend-menu{
  margin: 10px;
  
}
.menu-item{
    height: 50px;
    width: 250px;
   font-size: 22px;
   color: #000;
   margin: 20px;
   margin-left: 50px;
   margin-top: 27px;
    
   
   box-shadow: 0 0 20px 0 rgba(228, 86, 121, 0.938);
   background: rgb(240, 155, 119);
   border-radius: 25px;
  
}

 .icon{
position: relative;
top: 5px;


}
.icon link{
    text-decoration: none;
    color: #000;
}
.menu-item span{
    position: relative;
    top: 2px;

    font-family: 'Lobster', cursive;
    
}
.link a{

    text-decoration: none;
    color: #000;
}


.menu-item:hover{
    cursor: pointer;
    color: rgb(1, 12, 15);
    background: rgb(245, 223, 234);
}
/*second column*/
.navigation-bar2{
    width: 100%;
    height:50px;

    background-color: rgba(223, 237, 243, 0.67);
    color:rgb(223, 106, 16);
    align-items: center;
    text-align: center;
    display: flex;
    justify-content: space-between;
    
    
}
.navigation-links2 a{
    text-decoration: none;
    color:rgb(18, 2, 2);
    position: relative;
   
    font-size: 30px;
    font-family: 'Sofia';
    font-weight: bold;
    text-align: center;
    align-items: center;
    top: 10px;
    padding: 5px;
    transition:100ms ease-in;
}

.maindash2{
    height: 650px;
   width: 1150px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background: rgba(168, 226, 252, 0.67);
}
.cards{
    transform: translateY(25px);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color:rgb(10, 10, 10);
    margin-top: 25px;
}
.cards2{
     transform: translateY(25px);
