.footer{
    width:100%;
    height:200px;
    background-color: black;
    opacity: 0.9;
    color:white;
}
.footer h2{
   
    color:rgb(247, 247, 243);
    padding-bottom:7px;
    
}
.foot-row{
       display:flex;
       flex-direction: row;
       justify-content: space-around;
       padding: 20px;
}
.foot-col ul{
       list-style-type: none;
       padding: 0%;
       
}
.foot-col ul li{
    
    margin-bottom: 10px;
}
.foot-col ul li a{
    transition: 500ms ease-in;
}
.foot-col ul li a:hover{
    color:rgb(117, 6, 6);
    
    
}
.links li a{
    text-decoration: none;
    color: antiquewhite;
    font-size: 18px;
    font-family:serif;
    
}
.contact-links{
    display:flex;
    float:left;
    
}
.contact-links li a{
    padding-right: 10px;
    font-size: 30px;
    margin-top: 20px;
    color:white;
    transition: 100ms ease-in;
}
.contact-links li a:hover{
    color:rgb(88, 66, 66);
}
.footer2{
    width:100%;
    color:beige;
    background-color: black;
    
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}
