@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@500&display=swap');
.back{
    font-family: 'IBM Plex Sans Arabic', sans-serif;
    margin: 0;
    padding: 0;
    height: 85vh;
    width: 100%;
    text-align: center;
    align-items: center;
    
}

.mytext{
   
    display: flex;
    flex-direction: row;
    font-size: 15px;
}
.mytext h1{

    position: relative;
    left: 370px;
    top: 80px;
    font-weight: bold;
    
}
.mytext p{
    position: relative;
    top: 100px;
    right: 30px;
    margin: 3% ;
     font-size: 20px;
   
}
.back img{
    width: 40%;
    height: 400px;
    margin:5% 2%;
    opacity: 0.9;
    border-radius: 20%;
}
